import TimePicerComponent from './TimePicker.vue';

const TimePicker = {
  install: function (Vue) {
    if (typeof window !== 'undefined' && window.Vue) {
      Vue = window.Vue
    }
    Vue.component('TimePicker', TimePicerComponent)
  }
}

export default TimePicker;


