<template>
    <div class="timePicker">
        <input 
            type="text" 
            :readonly="readonly ? true : false" 
            class="form_datetime"
            :data-date-format="format || 'yyyy-mm-dd hh:ii'"
            :placeholder="placeholder || 'select any date...'"
            :id="`${dynamicId}`">
    </div>
</template>

<script>
/*
* @Author: zhenwei zhang 
* @Date: 2018-08-16 10:16:38 
 * @Last Modified by: zhenwei zhang
 * @Last Modified time: 2018-08-20 17:00:21
*  本组件对bootStrap DateTimePicker进行了封装，并对elementUI中的时间选择控件进行平滑过渡
*  这个组件是对 bootstraptimedatepicker的完整封装 see: http://www.bootcss.com/p/bootstrap-datetimepicker/index.htm
*/
import $ from 'jquery';
import C from './common.js';
import 'bootstrap/dist/css/bootstrap.css';
import './bootstrap-datetimepicker.js';
import './bootstrap-datetimepicker.css';

/*因为yarn下载下来的js有问题，所以直接将语言的闭包写在这里了*/
(function($){
	$.fn.datetimepicker.dates['zh-CN'] = {
			days: ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六", "星期日"],
			daysShort: ["周日", "周一", "周二", "周三", "周四", "周五", "周六", "周日"],
			daysMin:  ["日", "一", "二", "三", "四", "五", "六", "日"],
			months: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
			monthsShort: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
			today: "今天",
			suffix: [],
			meridiem: ["上午", "下午"]
	};
}(jQuery));

/*instance for datetimepicker*/
let picker = {
    props: [
        'type', 
        'placeholder', 
        'clearable', 
        'format', 
        'startDate', 
        'endDate', 
        'valueFormat', 
        'useCurrent', 
        'readonly', 
        'initialDate',
        'autoClose',
        'value',
        'options',
    ],
    name: 'TimePicer',
    data(){
        return{
            dynamicId: '', /*add dynamic id for multiple usage*/
            instance: {}, /*instance object*/
            date: this.defaultValue || '',
            pickerValue: '',
            minDate: '',
            maxDate: '',
            optionsDefault: {
                language:  'zh-CN',
                useCurrent: false,
                pickerPosition: 'bottom-left',
                autoClose: false,
                initialDate: '',
                minuteStep: 5
            },
            userOptions: {}
        }
    },
    created(){
        const jqueryObjectId = `dateTimePicker${this.random(1, 10000000000)}`;
        this.dynamicId = jqueryObjectId;
    },
    mounted() {
        $(`#${this.dynamicId}`).datetimepicker(this.userOptions);
        /*初始化监听事件，完成jquery和vue组件的双向绑定*/
        $(`#${this.dynamicId}`).on('changeDate',(ev)=>{
            if(ev.date){
                this.pickerValue = ev.date; /*转化时间*/
            }
        });
    },
    computed: {
        timeResult(){
            const format = this.valueFormat || 'timestamp';
            const time = this.pickerValue || '';
            if(format == 'timestamp'){
                /*return timestamp*/
                return C.formatUnix(time);
            }
            if(format == 'datetime'){
                /*return datetime*/
                return C.formatDateTime(time);
            }
            if(format == 'date'){
                /*return date*/
                return C.formatDate(time);
            }
        }
    },
    methods: {
        random(start, end){
            return Math.floor(Math.random() * (end - start) + start);
        },
        remove(){
           /*remove dom*/
           $(`#${this.dynamicId}`).datetimepicker('remove');
        },
        hide(){
           /*hide dom*/
           $(`#${this.dynamicId}`).datetimepicker('hide');
        },
        update(){
           /*refresh dom*/
           $(`#${this.dynamicId}`).datetimepicker('update');
        },
        setDaysOfWeekDisabled(range){
           $(`#${this.dynamicId}`).datetimepicker('setDaysOfWeekDisabled', range || null);
        },
        getInstance(){
            return `#${this.dynamicId}`;
        },
    },
    watch:{
        pickerValue(val){
            this.$emit('change', this.timeResult); /*emit change events*/
            this.$emit('input', this.timeResult); /*set v-model binding*/
        },
        options: {
            handler(opt){
                let {optionsDefault} = this;
                if(!opt){
                    this.userOptions = optionsDefault;
                }else{
                    this.userOptions = optionsDefault;
                    this.userOptions = Object.assign({}, this.userOptions, opt);
                }
                C.log('option modified: ', this.userOptions);
                this.remove(); /*reload*/
                $(`#${this.dynamicId}`).datetimepicker(this.userOptions);
            },
            deep: true,
            immediate: true
        },
        value(val){
            /*bind v-model*/
            if(!val){
                this.pickerValue = '';
                $(`#${this.dynamicId}`).val('');
            }else{
                $(`#${this.dynamicId}`).val(C.formatDateTime(val, 'YYYY-MM-DD HH:mm'));
            }
        },
    }
}
/* vue instance */
export default picker;
</script>

<style>
    .timePicker{
        position: relative;
    }
    .form_datetime{
        position: relative;
        -webkit-appearance: none;
        background-color: #fff;
        background-image: none;
        border-radius: 3px;
        border: 1px solid #dcdfe6;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: inherit;
        height: 32px;
        line-height: 32px;
        outline: 0;
        padding: 0 15px;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        width: 100%;
    }
</style>




